import {
  ADD_CATEGORY,
  REMOVE_CATEGORY,
  SET_ACTIVE_CATEGORY_INDEX,
  SET_CATEGORIES,
  SET_MODE,
  SET_CATEGORY_TITLE,
  SET_ALL_AVAILABLE_COMMANDS,
  SET_ACTIVE_CATEGORY_COMMAND_IDS,
  SET_POSITION_PIE_MENU_AT_CENTER,
  SET_PIE_MENU_POSITION,
  SET_CURSOR_POSITION,
  SET_HOVERED_COMMAND_INDEX,
} from '../actions/types';

const initialState = {
  mode: 0,
  categories: [],
  activeCategoryIndex: null,
  allAvailableCommands: [],
  allAvailableCommandIdsToCommands: {},
  positionPieMenuAtCenter: true,
  pieMenuPosition: [0, 0],
  cursorPosition: [0, 0],
  hoveredCommandIndex: null,
};

function mainReducer(state = initialState, action) {
  const { payload } = action;
  switch (action.type) {
    case SET_HOVERED_COMMAND_INDEX:
      return {
        ...state,
        hoveredCommandIndex: payload,
      };
    case SET_MODE:
      return {
        ...state,
        mode: payload,
      };
    case SET_CATEGORIES:
      return {
        ...state,
        categories: payload,
      };
    case ADD_CATEGORY: {
      let id = 0;
      let found = true;
      while (found) {
        found = false;
        for (let c of state.categories) {
          if (c.id === id) {
            found = true;
            ++id;
            break;
          }
        }
      }
      return {
        ...state,
        categories: [...state.categories, { ...payload, id }],
      };
    }
    case REMOVE_CATEGORY:
      return {
        ...state,
        categories: state.categories.filter((c, i) => i !== payload),
      };
    case SET_ACTIVE_CATEGORY_INDEX:
      return {
        ...state,
        activeCategoryIndex: payload,
        hoveredCommandIndex: null,
      };
    case SET_CATEGORY_TITLE:
      return {
        ...state,
        categories: state.categories.map((c, i) => (i === payload.index ? { ...c, title: payload.title } : c)),
      };
    case SET_ALL_AVAILABLE_COMMANDS: {
      const allAvailableCommandIdsToCommands = [];
      for (let c of payload) {
        allAvailableCommandIdsToCommands[c.id] = c;
      }
      return {
        ...state,
        allAvailableCommands: payload,
        allAvailableCommandIdsToCommands,
      };
    }
    case SET_ACTIVE_CATEGORY_COMMAND_IDS: {
      if (state.activeCategoryIndex === null) {
        return state;
      }
      const newCategories = [...state.categories];
      newCategories[state.activeCategoryIndex] = {
        ...newCategories[state.activeCategoryIndex],
        commandIds: payload,
      };
      return {
        ...state,
        categories: newCategories,
      };
    }
    case SET_PIE_MENU_POSITION:
      return {
        ...state,
        pieMenuPosition: payload,
      };
    case SET_POSITION_PIE_MENU_AT_CENTER:
      return {
        ...state,
        positionPieMenuAtCenter: payload,
      };
    case SET_CURSOR_POSITION:
      return {
        ...state,
        cursorPosition: payload,
      };
    default:
      return state;
  }
}

export default mainReducer;
