import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/PieMenuContext.module.css';
import { setCursorPosition, setHoveredCommandIndex, setActiveCategoryIndex } from '../actions/main';
import { IM_LABEL_OFFSET, IM_MIN_RADIUS, IM_RADIUS_SCALE, RAD_TO_DEG, DEG_TO_RAD } from '../utils/constants';
import NextCategoryIcon from '../assets/pie-menu-next.svg';
import PrevCategoryIcon from '../assets/pie-menu-prev.svg';

const CANVAS_SIZE = [100, 100];
const SAFE_RADIUS = 15;

class PieMenuContext extends Component {
  constructor(props) {
    super(props);
    this.resizeAnimProc = this.resizeAnimProc.bind(this);
    this.mouseMoveHandler = this.mouseMoveHandler.bind(this);
    this.renderIconMenu = this.renderIconMenu.bind(this);
    this.updateHoveredCommandIndex = this.updateHoveredCommandIndex.bind(this);
    this.handleNextClick = this.handleNextClick.bind(this);
    this.handlePrevClick = this.handlePrevClick.bind(this);
    this.renderPreviousNextButtons = this.renderPreviousNextButtons.bind(this);

    this.state = {
      size: [0, 0],
      origin: [0, 0],
    };
    this.wrapperRef = React.createRef();
    this.canvasRef = React.createRef();
  }

  handlePrevClick(e) {
    e.preventDefault();

    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return;
    }

    if (activeCategoryIndex > 0) {
      this.props.setActiveCategoryIndex(activeCategoryIndex - 1);
    }
  }

  handleNextClick(e) {
    e.preventDefault();

    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return;
    }

    if (activeCategoryIndex + 1 < categories.length) {
      this.props.setActiveCategoryIndex(activeCategoryIndex + 1);
    }
  }

  resizeAnimProc() {
    const wrapper = this.wrapperRef.current;
    if (!wrapper) {
      this.reqAnimId = window.requestAnimationFrame(this.resizeAnimProc);
      return;
    }
    const rect = wrapper.getBoundingClientRect();
    let { size, origin } = this.state;
    if (size[0] !== rect.width || size[1] !== rect.height || origin[0] !== rect.left || origin[1] !== rect.top) {
      size = [rect.width, rect.height];
      origin = [rect.left, rect.top];
      this.setState({ size, origin });
    }
    const canvas = this.canvasRef.current;
    if (canvas) {
      const context = canvas.getContext('2d');

      // Clear rect
      context.fillStyle = `rgba(0,0,0,0.0)`;
      context.clearRect(0, 0, CANVAS_SIZE[0], CANVAS_SIZE[1]);

      // Draw hollow circle
      context.lineWidth = 6;
      context.strokeStyle = '#313131';
      context.beginPath();
      context.arc(CANVAS_SIZE[0] * 0.5, CANVAS_SIZE[1] * 0.5, 20, 0, 2 * Math.PI);
      context.stroke();

      // Draw tracker
      const { pieMenuPosition, positionPieMenuAtCenter, cursorPosition, activeCategoryIndex, categories } = this.props;
      if (activeCategoryIndex !== null) {
        let canvasCenter;
        if (positionPieMenuAtCenter) {
          canvasCenter = [size[0] * 0.5, size[1] * 0.5];
        } else {
          canvasCenter = [pieMenuPosition[0] - origin[0], pieMenuPosition[1] - origin[1]];
        }
        const relCursorPos = [cursorPosition[0] - origin[0], cursorPosition[1] - origin[1]];

        var dx = relCursorPos[0] - canvasCenter[0];
        var dy = relCursorPos[1] - canvasCenter[1];
        var dist = Math.sqrt(dx * dx + dy * dy);

        if (dist > SAFE_RADIUS) {
          var theta = Math.acos(dx / dist);
          if (dy < 0) {
            theta = Math.PI * 2 - theta;
          }

          var theta = Math.acos(dx / dist);
          if (dy < 0) {
            theta = Math.PI * 2 - theta;
          }

          const numCommandIds = categories[activeCategoryIndex].commandIds.length;
          var da = numCommandIds === 0 ? 0 : Math.PI / numCommandIds;
          if (da < 15 * DEG_TO_RAD) {
            da = 15 * DEG_TO_RAD;
          }

          context.lineWidth = 11;
          context.strokeStyle = '#FF0000';
          context.beginPath();
          context.arc(CANVAS_SIZE[0] * 0.5, CANVAS_SIZE[1] * 0.5, 20, theta - da, theta + da);
          context.stroke();
        }
      }
    }
    this.reqAnimId = window.requestAnimationFrame(this.resizeAnimProc);
  }

  mouseMoveHandler(e) {
    this.props.setCursorPosition([e.clientX, e.clientY]);
  }

  componentDidMount() {
    this.reqAnimId = window.requestAnimationFrame(this.resizeAnimProc);
    this.props.setHoveredCommandIndex(null);
  }

  componentWillUnmount() {
    if (this.reqAnimId) {
      window.cancelAnimationFrame(this.reqAnimId);
      this.reqAnimId = null;
    }
  }

  updateHoveredCommandIndex() {
    const { positionPieMenuAtCenter, pieMenuPosition, categories, activeCategoryIndex, cursorPosition } = this.props;
    if (activeCategoryIndex === null) {
      this.props.setHoveredCommandIndex(null);
      return;
    }
    const numCommandIds = categories[activeCategoryIndex].commandIds.length;
    if (numCommandIds === null) {
      this.props.setHoveredCommandIndex(null);
      return;
    }

    if (numCommandIds.length === 1) {
      this.props.setHoveredCommandIndex(0);
      return;
    }

    const { size, origin } = this.state;
    let cx, cy;
    if (positionPieMenuAtCenter) {
      cx = size[0] * 0.5;
      cy = size[1] * 0.5;
    } else {
      cx = pieMenuPosition[0] - origin[0];
      cy = pieMenuPosition[1] - origin[1];
    }
    const px = cursorPosition[0] - origin[0];
    const py = cursorPosition[1] - origin[1];
    let dx = px - cx;
    let dy = py - cy;
    let d = Math.sqrt(dx * dx + dy * dy);
    if (d < SAFE_RADIUS) {
      this.props.setHoveredCommandIndex(null);
      return;
    }
    let angle = Math.acos(dx / d);
    if (dy < 0) {
      angle = Math.PI * 2 - angle;
    }
    let ci = Math.floor((angle * numCommandIds) / (Math.PI * 2) + 0.5);
    if (ci >= numCommandIds) {
      ci = 0;
    }
    this.props.setHoveredCommandIndex(ci);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.cursorPosition !== this.props.cursorPosition) {
      this.updateHoveredCommandIndex();
    }
  }

  renderIconMenu() {
    const {
      allAvailableCommandIdsToCommands,
      hoveredCommandIndex,
      categories,
      activeCategoryIndex,
      positionPieMenuAtCenter,
      pieMenuPosition,
    } = this.props;
    if (activeCategoryIndex === null) {
      return null;
    }
    const { commandIds } = categories[activeCategoryIndex];
    const numCommandIds = commandIds.length;
    if (numCommandIds === 0) {
      return null;
    }
    let cx, cy;
    if (positionPieMenuAtCenter) {
      const { size } = this.state;
      cx = size[0] * 0.5;
      cy = size[1] * 0.5;
    } else {
      const { origin } = this.state;
      cx = pieMenuPosition[0] - origin[0];
      cy = pieMenuPosition[1] - origin[1];
    }
    let radius = numCommandIds * IM_RADIUS_SCALE;
    if (radius < IM_MIN_RADIUS) {
      radius = IM_MIN_RADIUS;
    }
    let offset = (Math.PI * 2) / numCommandIds;
    const toRender = [];
    for (let i = 0; i < numCommandIds; ++i) {
      const commandId = commandIds[i];
      const c = allAvailableCommandIdsToCommands[commandId];
      if (!c) {
        continue;
      }
      let theta = offset * i;
      let cosa = Math.cos(theta);
      let sina = Math.sin(theta);
      let x = cx + radius * cosa;
      let y = cy + radius * sina;
      toRender.push(
        <div
          key={i}
          className={styles.iconMenuItem + (hoveredCommandIndex === i ? ' ' + styles.iconMenuItemHovered : '')}
          style={{
            top: y + 'px',
            left: x + 'px',
            backgroundColor: c.backgroundColor,
            transform: `rotate(${theta + Math.PI / 2}rad)`,
          }}
        ></div>
      );
    }

    return toRender;
  }

  renderPreviousNextButtons() {
    const { positionPieMenuAtCenter, pieMenuPosition, activeCategoryIndex, categories } = this.props;

    if (activeCategoryIndex === null) {
      return null;
    }

    let cx, cy;
    if (positionPieMenuAtCenter) {
      const { size } = this.state;
      cx = size[0] * 0.5;
      cy = size[1] * 0.5;
    } else {
      const { origin } = this.state;
      cx = pieMenuPosition[0] - origin[0];
      cy = pieMenuPosition[1] - origin[1];
    }

    return (
      <div
        className={styles.prevNextButtons}
        style={{
          left: cx,
          top: cy + 30,
        }}
      >
        <div className={styles.iconWrapper} onClick={this.handlePrevClick}>
          <img src={PrevCategoryIcon} className={styles.icon} alt="previous category" />
        </div>
        <div className={styles.iconWrapper} onClick={this.handleNextClick}>
          <img src={NextCategoryIcon} className={styles.icon} alt="next category" />
        </div>
      </div>
    );
  }

  render() {
    const { activeCategoryIndex, positionPieMenuAtCenter, pieMenuPosition } = this.props;
    if (activeCategoryIndex === null) {
      return null;
    }
    let top, left;
    if (positionPieMenuAtCenter) {
      const { size } = this.state;
      left = (size[0] - CANVAS_SIZE[0]) * 0.5;
      top = (size[1] - CANVAS_SIZE[1]) * 0.5;
    } else {
      const { origin } = this.state;
      left = pieMenuPosition[0] - origin[0] - CANVAS_SIZE[0] * 0.5;
      top = pieMenuPosition[1] - origin[1] - CANVAS_SIZE[1] * 0.5;
    }
    return (
      <div className={styles.wrapper} ref={this.wrapperRef} onMouseMove={this.mouseMoveHandler}>
        <canvas
          ref={this.canvasRef}
          className={styles.canvas}
          width={CANVAS_SIZE[0]}
          height={CANVAS_SIZE[1]}
          style={{
            top,
            left,
          }}
        ></canvas>
        {this.renderIconMenu()}
        {this.renderPreviousNextButtons()}
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    activeCategoryIndex: main.activeCategoryIndex,
    allAvailableCommands: main.allAvailableCommands,
    allAvailableCommandIdsToCommands: main.allAvailableCommandIdsToCommands,
    categories: main.categories,
    positionPieMenuAtCenter: main.positionPieMenuAtCenter,
    pieMenuPosition: main.pieMenuPosition,
    cursorPosition: main.cursorPosition,
    hoveredCommandIndex: main.hoveredCommandIndex,
  };
}

const mapDispatchToProps = {
  setCursorPosition,
  setHoveredCommandIndex,
  setActiveCategoryIndex,
};

export default connect(mapStateToProps, mapDispatchToProps)(PieMenuContext);
