export const TEST_CATEGORIES = [
  { id: 0, title: 'My favorite category', commandIds: [] },
  { id: 1, title: 'My other favorite category', commandIds: [] },
];

for (let i = 0; i < 20; ++i) {
  TEST_CATEGORIES[0].commandIds.push(i * 5);
}

for (let i = 0; i < 3; ++i) {
  TEST_CATEGORIES[1].commandIds.push(i * 30);
}

export const TEST_ACTIVE_CATEGORY_INDEX = 0;

export const TEST_ALL_AVAILABLE_COMMANDS = [];
for (let i = 0; i < 100; ++i) {
  const r = i * 2;
  const g = i * 2;
  const b = 255;
  TEST_ALL_AVAILABLE_COMMANDS.push({
    id: i,
    path: 'test_path',
    backgroundColor: `rgb(${r},${g},${b})`,
  });
}

